<template>
  <div class="contain">
    <div class="title">
      <el-button @click="callD" class="elButton" type="primary"
        >JSON2TS</el-button
      >
      <el-button @click="copy" class="elButton" type="warning">COPY</el-button>
    </div>
    <div class="content">
      <el-input
        v-model="fields"
        type="textarea"
        placeholder="Please input"
        class="elInput"
      />
      <el-input disabled v-model="word" type="textarea" class="elInput" />
    </div>
  </div>
</template>

<script>
const JsonToTS = require("json-to-ts");
import useClipboard from "vue-clipboard3";
const { toClipboard } = useClipboard();
import { ElMessage } from "element-plus";

export default {
  name: "JsonToTs",
  props: {
    msg: String,
  },
  data() {
    return {
      word: "",
      fields: "",
    };
  },
  methods: {
    callD() {
      try {
        const result = JSON.parse(this.fields);
        if (!result) {
          ElMessage({ message: "数据不能为空", type: "error" });
          return;
        }
        let word = "";
        JsonToTS(result).forEach((typeInterface) => {
          word = word + typeInterface + "\n";
        });
        this.word = word;
      } catch (e) {
        ElMessage({ message: "请查看您的数据是否正确", type: "error" });
      }
    },
    async copy() {
      try {
        await toClipboard(this.word);
        ElMessage({ message: "TS复制成功", type: "success" });
      } catch (e) {
        ElMessage({ message: "复制失败", type: "error" });
      }
    },
  },
};
</script>

